import { type NextConfig } from 'next'
import path from 'path'
import { KV } from './api/kv'

/**
 * @deprecated since version 1.5.0, use `WPEConfig` instead
 */
export interface AtlasConfig extends WPEConfig {}

export interface WPEConfig extends Record<string, any> {
  /**
   * If set to `false`, the Headless Platform remote cache handler will not be added (defaults to true)
   */
  remoteCacheHandler?: boolean
}

/**
 * This is an alias for withWPEConfig
 * @deprecated since version 1.5.0, use `withWPEConfig` instead
 * @param nextConfig
 * @param atlasConfig
 * @returns The modified config
 */
export function withAtlasConfig(
  nextConfig: NextConfig,
  atlasConfig?: AtlasConfig
): NextConfig {
  return withWPEConfig(nextConfig, atlasConfig)
}

/**
 * Add Headless Platform options to the config to be exported from the user's Next.js config file
 * @param nextConfig
 * @param wpeConfig
 * @returns The modified config
 */
export function withWPEConfig(
  nextConfig: NextConfig,
  wpeConfig?: WPEConfig
): NextConfig {
  if (wpeConfig?.remoteCacheHandler === false) {
    return nextConfig
  }

  /* eslint-disable @typescript-eslint/no-var-requires */
  // Get the next.js version
  const nextModulePath = path.parse(require.resolve('next'))
  const nextPackage = require(
    path.join(nextModulePath.dir, '../../package.json')
  )
  /* eslint-enable @typescript-eslint/no-var-requires */

  return setCacheHandler(
    nextConfig,
    nextPackage.version,
    // TODO: look closer how this can be stubbed to enable testing
    // of the withWPEConfig function
    require.resolve('@wpengine/atlas-next/cache-handler')
  )
}

export function setCacheHandler(
  nextConfig: NextConfig,
  nextVersion: string,
  cacheHandlerPath: string
): NextConfig {
  // If next.js version is less than 15.0.0
  if (compare(nextVersion, '15.0.0') === -1) {
    throw new Error(
      'Next.js version ' +
        nextVersion +
        ' is not supported by @wpengine/atlas-next'
    )
  }

  // If next.js version is 14.1.0 or higher
  if (nextConfig.cacheHandler !== undefined) {
    console.warn('Overwriting existing cacheHandler config')
  }
  nextConfig.cacheHandler = cacheHandlerPath
  if (nextConfig.cacheMaxMemorySize !== undefined) {
    console.warn('Overwriting existing cacheMaxMemorySize config')
  }
  nextConfig.cacheMaxMemorySize = 0

  printStartupNotice()

  return nextConfig
}

function printStartupNotice(): void {
  if (process.env.HEADLESS_CACHE_HANDLER_STARTUP === undefined) {
    let message = 'Headless Platform remote cache handler enabled'
    if (
      process.env.HEADLESS_METADATA_BUILD === undefined &&
      !KV.isAvailable()
    ) {
      message = message + ' (local storage mode)'
    }
    message = message + '\n'

    console.log(message)

    // Only print the message once
    process.env.HEADLESS_CACHE_HANDLER_STARTUP = 'true'
  }
}

// The function is created to not add an external dependency to the package.
export function compare(v1: string, v2: string): number {
  // Remove any pre-release tags
  const version1 = v1.split('-')[0]
  const version2 = v2.split('-')[0]

  validateVersion(version1)
  validateVersion(version2)

  const v1Parts = version1.split('.')
  const v1Major = v1Parts[0]
  const v1Minor = v1Parts[1]
  const v1Patch = v1Parts[2]
  const v2Parts = version2.split('.')
  const v2Major = v2Parts[0]
  const v2Minor = v2Parts[1]
  const v2Patch = v2Parts[2]

  const v1PatchLesserOrEqual = v1Patch < v2Patch ? -1 : 0
  const comparePatchVersions = v1Patch > v2Patch ? 1 : v1PatchLesserOrEqual
  const v1MinorLesserOrEqual = v1Minor < v2Minor ? -1 : comparePatchVersions
  const compareMinorVersions = v1Minor > v2Minor ? 1 : v1MinorLesserOrEqual
  const v1MajorLesserOrEqual = v1Major < v2Major ? -1 : compareMinorVersions

  return v1Major > v2Major ? 1 : v1MajorLesserOrEqual
}

export function validateVersion(version: string): void {
  const invalidVersionError = 'Unable to validate Next.js version: ' + version
  if (version.length === 0) {
    throw new Error(invalidVersionError)
  }
  const parts = version.split('.')
  if (parts.length < 3) {
    throw new Error(invalidVersionError)
  }
  const major = parts[0]
  if (major.length === 0 || isNaN(Number(major))) {
    throw new Error(invalidVersionError)
  }
  const minor = parts[1]
  if (minor.length === 0 || isNaN(Number(minor))) {
    throw new Error(invalidVersionError)
  }
}
